.Button {
    border-radius: 20%;
    -webkit-transition: background-color 0.5s;
    -o-transition: background-color 0.5s;
    transition: background-color 0.5s;
    width: 160px;
    height: 75px;
    line-height: 75px;
    text-align: center;
    cursor: pointer;
    -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
    transition: -webkit-transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
    -o-transition: transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
    transition: transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
    transition: transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92), -webkit-transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
}

.Button:hover {
    -webkit-transform: translate3d(0, -5px, 0);
            transform: translate3d(0, -5px, 0);
}

@media only screen and (max-width: 600px), screen and (max-height: 400px) {
.Button {
    width: 50px;
    height: 35px;
    line-height: 35px;
  }
}



