.Content {
    font-size: 1.5em;
    font-family: 'Unica One';
    color: #ddd;
    grid-area: cont;
    margin-top: 60px;
    margin-bottom: 50px;
    padding-left: 50px;
    padding-right: 50px;
    overflow: auto;
}

.Content h1, .Content h2, .Content h3 {
    font-size: 2.0em;
}

ol {
    padding-left: 100px;
    padding-right: 100px;
}

ol li {
    padding-top: 25px;
    padding-bottom: 25px;
}

.Content div {
    height: auto;
}

@media only screen and (max-width: 600px), screen and (max-height: 400px) {
    .Content {
        margin-top: 10px;
        margin-bottom: 10px;
        padding-left: 10px;
        padding-right: 10px;
    }
}

canvas {
	background: #eee;
	display: block;
    margin: 50px auto 0;
    width: 660px;
    height: 440px;
}

.prof {
    float: right;
}

.races {
    display: grid;
    grid-template-columns: repeat(5, 17%);
    grid-gap: 20px 3%;
    padding-bottom: 30px;
    text-align: center;
}

.race {
    height: 500px;
    position: relative;
	font-size: 0.5em;
	color: azure;
	border-color: darkblue;
	border-style: solid;
	border-width: medium;
	background-color: steelblue;
	border-radius: 0%;
    -webkit-transition: border-radius 0.5s, background-color 0.5s;
    -o-transition: border-radius 0.5s, background-color 0.5s;
    transition: border-radius 0.5s, background-color 0.5s;
}

.race img {
    height: 350px;
    width: 90%;
	border-color: deepskyblue;
	border-style: solid;
	border-width: medium;
	opacity: 0.5;
	-webkit-transition: opacity 0.5s, border-radius 0.5s;
	-o-transition: opacity 0.5s, border-radius 0.5s;
	transition: opacity 0.5s, border-radius 0.5s;
	z-index: -1;
}

.race:hover {
    border-radius: 10%;
    background-color: rgb(2, 70, 70);
}

.race img:hover {
	border-radius: 10%;
	opacity: 0.5;
}

.final-data td, .final-data {
    border-color: white;
	border-style: solid;
	border-width: medium;
}



