h1, h2, h3, p, figcaption {
  padding-top: 20px;
  padding-bottom: 20px;
}

h1 {
  font-size: 1.5em;
  color: #ff9720;
}

h2 {
  color: #999;
}

h3 {
  color: #df861d;
}

a {
  color: #fdc029;
  text-decoration: none;
}

a:hover {
  color: #894a00;
}

ul {
  list-style: none;
}

li {
  padding-top: 10px;
}

code {
  font-size: 1.25em;
  color: rgb(209, 209, 12);
  border-style: solid;
  border-width: 1px;
  border-color: #666;
  font: 'Courier New';
  background-color: #444;
}

.Container {
  display: grid;
  grid-template-areas:
    'aside header'
    'aside cont';
  grid-template-rows: 1fr 9fr;
  grid-template-columns: 200px auto;
}

@media only screen and (max-width: 600px), screen and (max-height: 400px) {
  .Container {
    grid-template-areas:
      'aside'
      'header'
      'cont';
    grid-template-rows: 80px 50px auto;
    grid-template-columns: 1fr;
  }
}

.PythonFacts {
  display: grid;
  grid-template-columns: 1fr 13fr 1fr;
  grid-gap: 20px;
  text-align: center;
  font-family: 'Courier New';
  font-size: 0.75em;
  vertical-align: middle;
}

.PythonFacts-panel {
  display: inline-block;
  height: 400px;
  overflow: hidden;
  background-color: #333;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.PythonFacts-panel:hover {
  background-color: #222;
}

.PythonFacts-left, .PythonFacts-right {
  display: inline-block;
  height: 400px;
  line-height: 400px;
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  transition: background-color 0.5s;
  background-color: #333;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.PythonFacts-left:hover, .PythonFacts-right:hover {
  background-color: #666;
}

.PythonFacts pre {
  color: rgb(209, 209, 12);
  background-color: #444;
  text-align: left;
  margin: auto;
  width: 400px;
  padding: 10px;
  border-style: solid;
  border-width: 2px;
  border-color: #666;
}

.PythonFacts ol {
  text-align: left;
  color: rgb(209, 209, 12);
  margin: auto;
  width: 600px;
  padding: 10px;
  padding-left: 40px;
  border-style: solid;
  border-width: 2px;
  border-color: #666;
}

.PythonFacts li {
  padding: 0;
}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}



